window.global = window;

import SimpleLightbox from 'https://cdn.jsdelivr.net/npm/simplelightbox/dist/simple-lightbox.esm.js';

const galleryContainer = document.querySelector('.gallery-container');

class Gallery {
  constructor(galleryContainer, galleryItems) {
    this.galleryContainer = galleryContainer;
    this.galleryItems = galleryItems;
  }
  renderGallery() {
    const galleryHTML = this.galleryItems
      .map(
        ({ preview, original, description }) => `
    
    <li class="gallery-item"> 
    <a class="gallery-link" href="${original}"> 
    <img 
    class="gallery-image" 
    src="${preview}" 
    data-source="${original}" 
    alt="${description}" 
    /> 
    </a> 
    </li>`
      )
      .join('');
    this.galleryContainer.innerHTML = galleryHTML;
  }
}
